import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/about",
    name: "About",
    component: () =>
      import("../views/About.vue")
  },
  {
    path: "/promotion/:title",
    name: "Promotion",
    component: () => import("../views/Promotion.vue"),
    props: true
  },
  {
    path: "/signUp",
    name: "SignUpView",
    component: () =>
      import("../views/SignUpView.vue"),
    props: true
  },
  {
    path: "/signIn",
    name: "SignInView",
    component: () =>
      import("../views/SignInView.vue"),
  },
];

const router = new VueRouter({
  routes
});

export default router;
