<template>
  <!-- Destinations Section displayed in Home view -->
  <section>
    <b-container>
      <b-row class="my-4">
        <!-- Header  -->
        <b-col md="4">
          <h2 class="display-inline">Destinations</h2>
        </b-col>
        <!-- Navigation  -->
        <b-col md="8">
          <b-nav>
            <b-nav-item active>Veitnam</b-nav-item>
            <b-nav-item>Mynamar</b-nav-item>
            <b-nav-item>Thailand</b-nav-item>
            <b-nav-item>Philippines</b-nav-item>
            <b-nav-item class="ml-auto font-italic">View All</b-nav-item>
          </b-nav>
        </b-col>
      </b-row>
      <!-- Destionations Carousel  -->
      <div class="card-carousel-wrapper">
        <div class="card-carousel--nav__left" @click="moveCarousel(-1)" :disabled="atHeadOfList"></div>
        <div class="card-carousel">
          <div class="card-carousel--overflow-container">
            <div
              class="card-carousel-cards"
              :style="{ transform: 'translateX' + '(' + currentOffset + 'px' + ')'}"
            >
              <div class="card-carousel--card" v-for="dest in getDestinations" :key="dest.id">
                <img :src="dest.img" />
                <div class="card-carousel--card--footer">
                  <p>{{ dest.title }}</p>
                  <div class="blur-effect" :style="blurEffectBg"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="card-carousel--nav__right" @click="moveCarousel(1)" :disabled="atEndOfList"></div>
      </div>
    </b-container>
  </section>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  data() {
    // To add more Carousel Items: edit window size number
    return {
      currentOffset: 0,
      // Number of Carousel Items
      windowSize: 8,
      // Width of Each Carousel Item = 200 (( image actual width)) + 10px ((padding))
      paginationFactor: 210,
      blurEffectBg: {
        backgroundImage: "url(" + require("../assets/blur-effect.jpg") + ")"
      }
    };
  },
  computed: {
    atEndOfList() {
      return (
        this.currentOffset <=
        this.paginationFactor *
          -1 *
          (this.getDestinations.length -
            Math.ceil((window.innerWidth * 0.5) / 210))
      );
    },
    atHeadOfList() {
      return this.currentOffset === 0;
    },
    ...mapGetters(["getDestinations"])
  },
  methods: {
    moveCarousel(direction) {
      // Find a more elegant way to express the :style. consider using props to make it truly generic
      if (direction === 1 && !this.atEndOfList) {
        this.currentOffset -= this.paginationFactor;
      } else if (direction === -1 && !this.atHeadOfList) {
        this.currentOffset += this.paginationFactor;
      }
    }
  }
};
</script>

<style scoped>
a.nav-link {
  color: #6c757d;
}

a.nav-link.active {
  color: #2d2d2d !important;
  font-weight: bold;
  border-bottom: 2px solid #e0ad1e;
}

.card-carousel-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0 40px;
  color: #666a73;
  position: relative;
}

.card-carousel {
  display: flex;
  justify-content: center;
  width: 100%;
}

.card-carousel--overflow-container {
  overflow: hidden;
}

.card-carousel--nav__left,
.card-carousel--nav__right {
  position: absolute;
  top: 45%;
  display: inline-block;
  width: 35px;
  height: 35px;
  padding: 10px;
  box-sizing: border-box;
  background-color: #fff;
  border-top: 2px solid #42b883;
  border-right: 2px solid #42b883;
  cursor: pointer;
  margin: 0 20px;
  z-index: 10;
  transition: transform 150ms linear;
}

.card-carousel--nav__left[disabled],
.card-carousel--nav__right[disabled] {
  opacity: 0.2;
  border-color: black;
}

.card-carousel--nav__left {
  transform: rotate(-135deg);
  left: -37px;
}

.card-carousel--nav__left:active {
  transform: rotate(-135deg) scale(0.9);
}

.card-carousel--nav__right {
  transform: rotate(45deg);
  right: -37px;
}

.card-carousel--nav__right:active {
  transform: rotate(45deg) scale(0.9);
}

.card-carousel-cards {
  display: flex;
  transition: transform 150ms ease-out;
  transform: translatex(0px);
}

.card-carousel-cards .card-carousel--card {
  margin: 0 5px;
  cursor: pointer;
  box-shadow: 0 4px 15px 0 rgba(40, 44, 53, 0.06),
    0 2px 2px 0 rgba(40, 44, 53, 0.08);
  background-color: #fff;
  border-radius: 4px;
  z-index: 3;
  margin-bottom: 2px;
  position: relative;
}

.card-carousel-cards .card-carousel--card:first-child {
  margin-left: 0;
}

.card-carousel-cards .card-carousel--card:last-child {
  margin-right: 0;
}

.card-carousel-cards .card-carousel--card img {
  vertical-align: bottom;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  transition: opacity 150ms linear;
  user-select: none;
}

.card-carousel-cards .card-carousel--card img:hover {
  opacity: 0.5;
}

.card-carousel-cards .card-carousel--card--footer {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 45px;
  width: 100%;
  overflow: hidden;
}

.card-carousel-cards .card-carousel--card--footer p {
  font-size: 22px;
  font-weight: 500;
  color: #fff;
  user-select: none;
  position: absolute;
  z-index: 10;
  width: 100%;
  text-align: center;
  margin-top: 6px;
}

.card-carousel-cards .card-carousel--card--footer .blur-effect {
  border-top: 0;
  position: absolute;
  bottom: -13px;
  left: -20px;
  text-align: center;
  width: 110%;
  height: 60px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  -webkit-filter: blur(39px);
  filter: blur(39px);
}
</style>