export default {
  state: {
    users: [],
    showSignIn: false
  },
  actions: {
    addNewUser({ commit }, userCredentials) {
      commit("addUser", userCredentials)
    },
    toggleShowSignIn({ commit }) {
      commit("getterToggleShowSignIn")
    }
  },
  mutations: {
    addUser: (state, userCredentials) => state.users.push(userCredentials)
  },
  getters: {
    getUsers: state => state.users,
    getterToggleShowSignIn: state => state.showSignIn === !state.showSignIn
  }
};
